.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
div{box-sizing: border-box;}
.list{padding-left:20px;box-sizing: border-box;text-align: left;border-bottom:1px solid grey;}
.list h4{cursor: pointer;}
.black-nav{display:flex;background:black;width:100%;color:#fff;padding-left:20px;}
.modal{margin-top:20px;padding:20px;background:#eee;text-align:left;}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.list>h4>span{cursor: pointer;}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
